







import { Component, Vue } from "vue-property-decorator";
import GlobalFooter from "@/components/global/GlobalFooter.vue";

@Component({
  components: {
    GlobalFooter,
  },
})
export default class WebtoonList extends Vue {
  protected mounted() {
    Vue.prototype.$global = this;
    this.$el.addEventListener("scroll", this.bounceLock);
    setTimeout(() => {
      let disEl = this.disableElement(
        this.$el.querySelector(".view") as HTMLElement
      );
    }, 500);
  }

  public bounceLock(e: any) {
    const scrollY = this.$el.scrollTop;
    if (scrollY <= 0) {
      const view = this.$el.querySelector(".view") as HTMLElement;
      view.style.top = `${view.getBoundingClientRect().top}px`;
      view.style.position = "fixed";
      setTimeout(() => {
        (this.$el.querySelector(".view") as HTMLElement).style.position =
          "static";
      }, 1);
    }
  }

  private a11y: any = [];
  public disableElement(target: HTMLElement) {
    const parent = target.parentNode;
    this.a11y.push(Array.from(parent!.childNodes));
    const index = this.a11y.length - 1;
    console.log(this.a11y[index]);
    // this.a11y[index].forEach(( el ) => {

    // // (el as HTMLElement).getAttribute('aria-hidden');
    //   if (el != target) {
    //     (el as HTMLElement).tabIndex = -1;
    //     (el as HTMLElement).setAttribute('aria-hidden', 'true');
    //   }
    // })

    return index;
  }
}
