import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

Vue.config.productionTip = false;
declare module "vue/types/vue" {
  // 3. Vue에 보강할 내용을 선언하세요.
  interface Vue {
    $global: any;
  }
}

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
