import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import webtoon from "./webtoon"
import recommend from "./recommend"
import best from "./best"
import my from "./my"
import common from "./common"


Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    redirect: '/webtoon',
  },
  {
    path: "/webtoon",
    component: () =>
      import(/* webpackChunkName: "webtoon" */ "@/views/webtoon/index.vue"),
    children: webtoon,
  },
  {
    path: "/recommend",
    component: () =>
      import(/* webpackChunkName: "recommend" */ "@/views/recommend/index.vue"),
    children: recommend,
  },
  {
    path: "/best",
    component: () =>
      import(/* webpackChunkName: "best" */ "@/views/best/index.vue"),
    children: best,
  },
  {
    path: "/my",
    component: () =>
      import(/* webpackChunkName: "my" */ "@/views/my/index.vue"),
    children: my,
  },
  {
    path: "/common",
    component: () =>
      import(/* webpackChunkName: "common" */ "@/views/common/index.vue"),
    children: common,
  },
  {
    path: "/icon",
    component: () =>
      import(/* webpackChunkName: "icon" */ "@/components/icons.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL + '/dist',
  routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
});

export default router;
